.App {
  text-align: center;
  height: 100%;
  margin: 40px auto;
  padding: 10px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.timerContainer {
  height: 100%;
  position: relative;
}


.circleProgress {
  max-height: 500px;
  max-width: 60%;
  margin: 40px auto;
  z-index: 1;
}

.counterContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-49%, -100%);
      -ms-transform: translate(-49%, -100%);
          transform: translate(-49%, -100%);
}

.workPlayIcon {
  font-size: 2em;
  margin: 0px;
  margin-bottom: 20px;
  color: #2d2d2d;
}

.counter {
  font-size: 4em;
  color: #d67e73;
  margin: 0;
}


.actionBtn {
  padding: 1.1em;
  border-radius: 100%;
  cursor: pointer;
  border: none;
  -webkit-box-shadow: -2px -3px 12px rgba(0, 0, 0, 0.05), inset -1px -7px 2px rgba(0, 0, 0, 0.06);
          box-shadow: -2px -3px 12px rgba(0, 0, 0, 0.05), inset -1px -7px 2px rgba(0, 0, 0, 0.06);;
  margin: 0 10px;
  background-color: #d67e73;
  color: white;
  font-size: 1.5em;
  -webkit-transition: 150ms ease-out;
  -o-transition: 150ms ease-out;
  transition: 150ms ease-out;
  -webkit-tap-highlight-color: transparent;
}
.actionBtn:hover {
  color: #f2f2f2;
}
.actionBtn:active {
  color: #ddd;
  -webkit-box-shadow: inset 0px 0px 0px rgba(0, 0, 0, 0.23);
          box-shadow: inset 0px 0px 0px rgba(0, 0, 0, 0.23);
}
.actionBtn:focus {
  outline: 0;
}


@media (max-width: 600px) {
  .counterContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -110%);
        -ms-transform: translate(-50%, -110%);
            transform: translate(-50%, -110%);
  }

  .workPlayIcon {
    font-size: 1.3em;
    margin-bottom: 10px;
  }

  .counter {
    font-size: 2.2em;
  }

  .actionBtn {
    padding: 1.1em;
    margin: 0 10px;
    font-size: 1em;
  }
}



